import { createWebHistory, createRouter } from "vue-router";
import List from './components/List.vue'
import myHome from './components/myHome.vue'
import detail from './components/articleDetail.vue'
import blogAuthor from './components/author.vue'
import blogComment from './components/comment.vue'

const routes = [
  {
    path: "/list",
    component: List,
  },
  {
    //path, component 설정하면 됨.
    path: "/",
    component: myHome
  },
  {
    //path, component 설정하면 됨.
    //파라미터 뒤에 ()를 사용하여 정규표현식 사용 가능
    path: "/detail/:id(\\d+)",
    component: detail,
    children: [
      {
        //nested routes : detail/0/author
        path: 'author',
        component: blogAuthor
      },
      {
        //nested routes : detail/0/author
        path: 'comment',
        component: blogComment
      },
    ]
  },  
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 