<template>
  <div v-for="(item,i) in item" :key="i">
    <h5 @click="move(i)">{{item.title}}</h5>
    <p>{{item.content}}</p>
  </div>
</template>

<script>
export default {
    name:'articleList',
    data(){
      return {
        id: 0,
      }
    },
    props: {
        item: Object,
    },
    methods: {
      move(i){
        var url = '/detail/' + i;
        this.$router.push(url);
      }
    }
}
</script>

